
html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
}
body {
    font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', SimSun, sans-serif;
}
* {
    padding: 0;
    margin: 0;
}
.wrapper {
    height: 100%;
    display: block;
}
.main {
    height: 100%;
}
a {
    text-decoration: none;
}
header {
    width: 100%;
    padding: 0 20px;
    z-index: 1;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
header::after {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle
}
.container {
    padding-top: 80px;
}
.menu {
    height: 100%;
}
.content {
    padding: 20px;
}
.header-logo {
    display: inline-block;
    vertical-align: middle;
}
.header-operations {
    display: inline-block;
    float: right;
    padding-right: 30px;
    height: 100%;
}
.header-operations li {
    color: #fff;
    display: inline-block;
    vertical-align: middle;
    padding: 0 10px;
    margin: 0 10px;
    line-height: 80px;
    cursor: pointer;
}

/* .header-operations li:last-child {
    cursor: default;
} */
.header-operations span {
    opacity: 0.7;
}
.header-operations::after {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle
}
.theme-form {
    text-align: center;
}
.query-form {
    margin-top: 20px;
    padding-top: 25px;
    padding-left: 10px;
    background-color: #f2f2f2;
}
.table {
    margin-top: 30px;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}
.avatar {
    width: 178px;
    height: 178px;
    display: block;
}
.el-menu {
    height: 100%;
}



























































































































































































































































.login-container {
